* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  background-color: #0f1614;
  align-items: stretch;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header {
  visibility: hidden;
  font-family: Permanent Marker;
  display: none;
  position: absolute;
  top: 0;
  left: 15px;
}

.hidden {
  visibility: hidden;
  display: none;
}

.content {
  z-index: 0;
  height: calc(100vh - 20px);
  width: 100vw;
  background: #073030;
  position: absolute;
  top: 20px;
}

.device-info {
  z-index: 1;
  opacity: 1;
  color: #bbcebb;
  color: #fff;
  align-items: left;
  word-wrap: break-word;
  background-color: #000000c3;
  border-width: 0;
  flex-direction: column;
  margin: 10px;
  padding: 20px;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  position: absolute;
  top: 25vh;
  left: 5vw;
  right: 5vw;
  overflow: auto;
}

.device-info-details {
  max-height: 45vh;
  border: 2px dotted #fff;
  margin-top: 20px;
  font-size: 14px;
  display: block;
  overflow: auto;
}

.search-bar {
  z-index: 1;
  opacity: .9;
  align-items: stretch;
  display: flex;
  position: absolute;
  top: 40px;
  left: 60px;
}

.search-input {
  height: 1em;
  max-width: calc(100vw - 120px);
  color: #000;
  opacity: .65;
  background-color: #fff;
  border-style: none;
  border-width: 0;
  padding: 20px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 100;
}

.search-input:hover, .search-input:focus {
  opacity: .9;
}

.search-status {
  z-index: 2;
  opacity: .75;
  color: #fff;
  justify-content: left;
  font-size: 12px;
  display: block;
  position: fixed;
  top: 90px;
  left: 60px;
}

.search-hint {
  z-index: 2;
  opacity: .75;
  max-width: 80vw;
  max-height: 40vh;
  color: #fff;
  background-color: #000;
  border-style: none;
  border-width: 0;
  justify-content: left;
  padding: 10px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 100;
  display: block;
  position: fixed;
  top: 120px;
  left: 60px;
  overflow: auto;
}

.loading-bar {
  opacity: .9;
  z-index: 2;
  color: #fff;
  width: 60vw;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  margin-bottom: 20px;
  padding: 0;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 200;
  display: flex;
  position: absolute;
  top: 20vh;
  bottom: 10vh;
  left: 20vw;
}

progress::-moz-progress-bar {
  background: #fff;
}

progress::-webkit-progress-value {
  background: #fff;
}

progress {
  color: #fff;
}

.loading-progress-bar {
  width: 100%;
  height: 8px;
  color: #fff;
  background-color: gray;
  border-radius: 0;
  margin: 10px;
}

.loading-details {
  width: 100%;
  height: 100%;
  color: #fff;
  background-color: #00000059;
  border-width: 1px;
  margin: 10px;
  padding: 20px;
  font-family: Roboto;
  font-size: 10px;
  font-weight: 400;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #0000004d;
}

::-webkit-scrollbar-thumb {
  background-color: #a9a9a9;
  outline: 1px solid #708090;
}

textarea::-webkit-scrollbar {
  width: 1em;
}

textarea::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px #0000004d;
}

textarea::-webkit-scrollbar-thumb {
  background-color: #a9a9a9;
  outline: 1px solid #708090;
}

.status-area {
  z-index: 1;
  color: #fff;
  flex-direction: row;
  margin: 0;
  padding: 0;
  font-size: 15px;
  display: flex;
  position: fixed;
  top: 2px;
  right: 5px;
}

.green-dot {
  height: 15px;
  width: 15px;
  color: #314d39;
  vertical-align: middle;
  background-color: #42724f;
  border-width: 1px;
  border-color: #fff;
  border-radius: 50%;
  margin: 0 0 0 5px;
  display: table-cell;
  right: 0;
}

.footer-logo {
  font-family: Permanent Marker;
  position: relative;
  top: -2px;
}

.footer-left {
  z-index: 1;
  height: 20px;
  color: #c0bfbfe5;
  margin: 0;
  padding: 0;
  font-size: 18px;
  position: fixed;
  bottom: 3px;
  left: 5px;
}

.footer-right {
  z-index: 1;
  height: 20px;
  color: #c0bfbfe5;
  margin: 0;
  padding: 0;
  font-size: 10px;
  position: fixed;
  bottom: 0;
  right: 5px;
}

@media only screen and (min-width: 550px) {
  .header {
    visibility: visible;
    z-index: 0;
    height: 40px;
    color: #fff;
    text-align: left;
    background: #0f1614;
    margin: 0;
    padding: 0;
    font-family: Permanent Marker;
    font-size: 30px;
    line-height: 40px;
    text-decoration: none;
    display: inline;
  }

  .device-info {
    right: auto;
  }

  .footer-left {
    height: 20px;
    font-size: 18px;
    bottom: 2px;
    left: 5px;
  }

  .footer-right {
    height: 20px;
    font-size: 18px;
    bottom: 2px;
    right: 5px;
  }

  .content {
    height: calc(100vh - 40px);
    top: 40px;
  }

  .search-bar {
    top: 60px;
  }

  .search-input {
    width: 35vw;
  }

  .search-status {
    top: calc(1em + 96px);
    left: 60px;
  }

  .status-area {
    font-size: 18px;
    top: 12px;
    right: 15px;
  }

  .green-dot {
    height: 18px;
    width: 18px;
  }

  .loading-details {
    font-size: 14px;
    font-weight: 100;
  }
}

.modal {
  z-index: 1;
  width: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  width: 100%;
  background-color: #fefefe;
  font-size: 24px;
  animation-name: slideIn;
  animation-duration: .4s;
  position: fixed;
  bottom: 20px;
}

.setup-submit {
  padding: 0 24px;
  font-size: 24px;
}

.close {
  color: #fff;
  float: right;
  opacity: .6;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 100;
  position: absolute;
  top: 0;
  right: 5px;
}

.close:hover, .close:focus {
  color: #fff;
  opacity: .8;
  cursor: pointer;
  font-weight: 400;
}

.modal-header {
  color: #fff;
  background-color: #5cb85c;
  padding: 2px 16px;
}

.modal-body {
  padding: 2px 16px;
}

.modal-footer {
  color: #fff;
  background-color: #5cb85c;
  padding: 2px 16px;
}

@keyframes slideIn {
  from {
    opacity: 0;
    bottom: -300px;
  }

  to {
    opacity: 1;
    bottom: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeOut {
  animation-name: fadeOut;
  animation-duration: 2s;
}

@keyframes fadeOut {
  to {
    opacity: 0;
  }

  from {
    opacity: 1;
  }
}

.modal-shadow {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  animation-name: fadeIn;
  animation-duration: .4s;
  position: absolute;
}

.center-modal {
  z-index: 1;
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
  position: absolute;
  inset: 8vh 0 0;
}

